/* Header */

.header-container {
  width: 100%;
  height: 100px;
  background-color: #F5F5F5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 9999;
}

.header-title {
  font-size: 40px;
  font-weight: 600;
  color: #000000;
}

.header-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-right: 20px;
}

.hero-image {
  width: 100vw;
  height: 600px;
  object-fit: cover;
}

.header-items {
  font-weight: 200;
  font-size: 20px;
  color: #000;
  text-decoration: none;
}

.header-items:hover {
  text-decoration: underline;
  text-shadow: 0 0 2px 1 #000;
}

.header, .footer {
  font-weight: 200;
  font-size: 20px;
  color: #000;
  margin-left: 20px;
}

.header h1,
.header p {
  margin: 0;

}

/* Footer */
.footer-container {
  width: 100%;
  height: max-content;
  position: relative;
  text-align: center;
}

/* Container */

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.inner-container {
  width: 90vw;
  max-width: 1200px;
}

body {
  background-color: #FFAA1D;
}

.inner-hero-container {
  position: absolute;
  top: 150px;
  left: 50px;
  color: white;
  width: 800px;
  z-index: 0;
}

.hero-title {
  font-size: 60px;
  font-weight: 1000;
  color: white;
  /* add text shadow */
  text-shadow: 2px 2px #000;
}

.hero-content-paragraph {
  font-size: 20px;
  font-weight: 500;
  color: white;
  font-size: 30px;
  text-shadow: 2px 2px #000;
}

.hero-button-container {
  display: flex;
  align-items: center;
  gap: 20px;
  flex-direction: row;
}

.hero-button {
  border: 2px solid #000000;
  background-color: white;
  border-radius: 30px;
  min-width: 200px;
  font-weight: 200;
  height: 50px;
  font-size: 20px;
  padding: 10px;
}

.element-items {
  display: flex;
  align-items: center;
  gap: 30px;
}

.element-image {
  width: 500px;
  height: 500px;
  object-fit: cover;
  border-radius: 50%;
}

.element-paragraph {
  font-size: 25px;
  font-weight: 500;
  color: #fff;
  width: 500px;
}

.sub-section {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.sub-section p {
  margin: 0;
  text-decoration: none;
  color: black
}

.recipe h2 {
  margin: 0;
  text-decoration: none;
  color: black
}

.recipe {
  background-color: #fff;
  width: 100%;
  gap: 10px;
  padding: 10px;
  text-decoration: none;
}

.recipe-image {
  width: 100%;
  height: 500px;
  object-fit: cover;
}

/* Community Section */
.comunity-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
  margin: 0px;
}

.community-header-title {
  font-size: 40px;
  font-weight: 600;
  color: #000000;
}

.community-image {
  width: 500px;
  height: 500px;
  object-fit: contain;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 100%;
  margin: 0px;
}

.form input {
  width: 100%;
  height: 30px;
  border: 2px solid #000000;
  border-radius: 20px;
  font-size: 20px;
  padding: 10px;
}

.form button {
  width: 100%;
  height: 30px;
  border: 2px solid #000000;
  border-radius: 20px;
  font-size: 15px;
  background-color: #F5F5F5;
}

.community-content {
  width: 100%;
  margin: 0;
}

.community-content p {
  font-size: 20px;
  font-weight: 500;
  color: #000000;
}

.community-content h3 {
  font-size: 40px;
  font-weight: 600;
  color: #000000;
}